<template>
  <div class="show-blogs">
    <h3>博客总览</h3>
    <input type="search" placeholder="搜索" v-model="search">
    <div class="single-blog" v-for="blog in filteredBlogs" :key="blog.id">
       <router-link v-bind:to="'/blog/'+blog.id"> <h4 v-anycolor>{{blog.title}}</h4></router-link>
        <article>{{blog.body | snippet}}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data () {
    return {
        blogs:[],
        search:""
      
    }
  },
  created(){
      
      this.$http.get("http://jsonplaceholder.typicode.com/posts")
      .then(function(data){
          this.blogs = data.body.slice(0,10);
         
      })
  },
  computed:{
      //搜索框过滤  在blogs数组这过滤出符合条件的返回去形成数组
      filteredBlogs:function(){
          return this.blogs.filter((blog)=>{
              return blog.title.match(this.search)
          })
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.show-blogs{
    max-width: 800px;
    margin: 20px auto;
}
.single-blog{
    width: 100%;
    background: #a0a0a0;
    padding: 20px;
    box-sizing: border-box;
    margin: 20px 0;
    border-radius: 5px;
}

input{
    display: block;
    padding: 5px;
    width: 300px;

}
a{
    text-decoration: none;
}
h4{
    font-size: 18px;
    transition: all 0.4s;
    
}
h4:hover{
    transform: scale(1.05);
}
</style>


