<template>
  <div class="add-blog">
    <h3>写博客</h3>
    <form v-show="!display">
        <label>主题</label>
        <input type="text" v-model="blogs.title">
        <label >内容</label>
        <textarea v-model="blogs.body"></textarea>
        <label >分类</label>
        <div class="checkbox">
            <label>
               Vue.js: <input type="checkbox" value="Vue.js" v-model="blogs.kinds">
            </label>
            <label>
               Node.js: <input type="checkbox" value="Node.js" v-model="blogs.kinds">
            </label>
            <label>
               Angular4: <input type="checkbox" value="Angular4" v-model="blogs.kinds">
            </label>
            <label>
               React.js: <input type="checkbox" value="React.js" v-model="blogs.kinds">
            </label>
            
        </div>
        <label >作者：</label>
        <select v-model="blogs.author">
            <option v-for="author in authors" :key="author">{{author}}</option>
        </select>
        <button v-on:click.prevent="post">添加博客</button>
    </form>
    <div v-show="display">
        <h3>博客发布成功</h3>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
        blogs:{
            title:"",
            body:"",
            kinds:[],
            author:""
        },
      authors:["小明","小萌","大明"],
      display:false
    }
  },
//   https://jsonplaceholder.typicode.com/posts
// {
//               title:this.blogs.title,
//               body:this.blogs.body,
//               userId:1
//           }
  methods:{
      post:function(){
          this.$http.post("https://jsonplaceholder.typicode.com/posts",{
              title:this.blogs.title,
              body:this.blogs.body,
              userId:1
          }).then(function(data){
              this.display=true;
             
          })
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.add-blog *{
    box-sizing: border-box;
}
.add-blog{
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    
}
input[type="text"],textarea,select{
    width: 100%;
    padding: 8px;
    display: block;

}
textarea{
    height: 300px;
}
label{
    display: block;
    margin: 20px 0 10px;
}
.checkbox label{
    display: inline-block;
    margin-top: 0;
}
.checkbox label input{
    margin-right: 10px;
}
button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
}
</style>


