<template>
  <div class="single-blog">
    <h1>{{blog.title}}</h1>
		<article>{{blog.body}}</article>
  </div>
</template>

<script>
export default {
  name: 'single-blog',
  data () {
    return {
      id:this.$route.params.id,
      blog:{}
    }
  },
  created(){
      this.$http.get("https://jsonplaceholder.typicode.com/posts/" + this.id)
      .then(function(data){
          this.blog=data.body;
          console.log(data);
          
      })
  }
  
      
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
ul{
	list-style-type: none;
	text-align: center;
	margin: 0;
}
li{
  display: inline-block;
  margin: 0 10px;
}

a{
  color: #fff;
  text-decoration: none;
  padding: 12px;
  border-radius: 5px;
}
nav{
	background: crimson;
	padding: 30px 0;
	margin-bottom: 40px;
}

.router-link-active{
	background: rgba(255,255,255,0.8);
	color: #444;
}
</style>


